.overlay{
    backdrop-filter: blur(5px);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 100;
}

.modal-container{
    background-color: var(--bg-alt);
    width: 30%;
    height: 30%;
    display: flex;
    padding: 5vh 0;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    border-radius: var(--border-radius);
}