.card{
    width: 25rem;
    height: 15rem;
    border-radius: var(--border-radius);
    background-color: white;
    margin-top: 3vh;
    transition: var(--speed);
    overflow: hidden;
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}

.card:hover{
    transform: scale(102%);
    transition: var(--speed);
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

.card img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.box{
    position: absolute;
    background-color: rgba(50, 50, 50, 0.5);
    width: 15rem;
    height: 10rem;
    margin-left: 5rem;
    margin-top: -12.5rem;
    border-radius: var(--border-radius);
    display: flex;
    justify-content: center;
    align-items: center;
}

.card h2{
    font-size: 2rem;
}