*{
  margin: 0;
  padding: 0;
  font-family: "Futura-CondensedExtraBold", sans-serif;
  box-sizing: border-box;
  color: var(--text-color);
}

body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  :root {
    --bg:  #242526;
    --bg-alt: #303132;
    --bg-accent: #484a4d;
    --accent: #4682B4;
    --accent-hover: #0096FF;
    --text-color: #dadce1;
    --nav-color: #202020;
    --nav-size: 60px;
    --border: 1px solid #474a4d;
    --border-radius: 8px;
    --speed: 500ms; 
  }
  
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  a {
    color: var(--text-color);
    text-decoration: none;;
  }

  .background{
    background: var(--bg);
    min-height: 100vh;
    background-size: 100vw;
    object-fit: cover;
    z-index: -5;
  }
  
  .stripes {
    width: 100%;
    position: absolute;
    top: 23vh;
  }

  .stripe {
    width: 100%;
    height: 3rem;
    background-color: var(--bg-alt);
    margin-top: 3rem;
    z-index: -5;
  }