.home_container{
    position: absolute;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 3rem;
}

.home_container h1{
    font-size: 3.5rem;
    margin-top: 3vh;
    width: 40rem;
    color: var(--text-color);
    text-transform: uppercase;
    font-weight: 800;
}

.project-holder{
    display: flex;
    flex-direction: row;
}

.card{
    margin: 2rem;
}